import {
	ScrollView,
	StyleSheet,
	Text,
	TouchableOpacity,
	View,
	FlatList,
	Image,
	TextInput,
} from 'react-native';
import React, {
	useCallback,
	useMemo,
	useRef,
	useState,
	useEffect,
} from 'react';
import Loader from '../components/common/Loader';
import ApiHandler from '../api/ApiHandler';
import Heading from '../components/common/Heading';
import SearchBar from '../components/common/SearchBar';
import Header from '../components/common/Header';
import { COLORS } from '../Theme/Theme';
import RangeSlider, { Slider } from 'react-native-range-slider-expo';

const years = [
	{ id: 1, year: '2012' },
	{ id: 2, year: '2013' },
	{ id: 3, year: '2014' },
	{ id: 4, year: '2015' },
	{ id: 5, year: '2016' },
	{ id: 6, year: '2017' },
	{ id: 7, year: '2018' },
	{ id: 8, year: '2019' },
	{ id: 9, year: '2020' },
	{ id: 10, year: '2021' },
	{ id: 11, year: '2022' },
];

const kms_Data = [
	{
		id: 1,
		kms: 'under 30,000 Km',
	},
	{
		id: 2,
		kms: '30,000 - 60,000 Km',
	},
	{
		id: 3,
		kms: ' 60,000 - 90,000 Km',
	},
	{
		id: 4,
		kms: '90,000 Km+',
	},
];

const UsedCarModel = ({ navigation, route }) => {
	const [cardata, setCardata] = useState([]);
	const [isloading, loading] = useState(true);
	const [formdata, setFormdata] = useState({
		barnd: route.params.data.brand,
	});
	const [fromValue, setFromValue] = useState(0);
	const [toValue, setToValue] = useState(0);
	const [value, setValue] = useState(0);

	console.log(cardata);
	useEffect(() => {
		getCarData(route.params.data.id);
	}, []);

	const getCarData = async (e) => {
		const response = await ApiHandler.getVarientsList('/' + e);
		setCardata(response.data);
		loading(false);
	};
	return (
		<>
			{isloading ? (
				<Loader />
			) : (
				<ScrollView
					showsVerticalScrollIndicator={false}
					style={{ backgroundColor: '#fff' }}
				>
					<View>
						<Header />
						<>
							<SearchBar navigation={navigation} />
							<Heading text={'Select Your Brand'} />
							<FlatList
								data={cardata}
								// keyExtractor={(key) => key}
								numColumns={3}
								renderItem={({ item, index }) => (
									<TouchableOpacity
										style={{
											alignItems: 'center',
											justifyContent: 'space-between',
											flex: 1,
											margin: 5,
											borderRadius: 12,
											shadowColor: 'gray',
											shadowOpacity: 0.26,
											shadowOffset: { width: 0, height: 2 },
											shadowRadius: 10,
											elevation: 3,
										}}
										onPress={() => {
											navigation.navigate('');
										}}
									>
										<Text
											style={{
												padding: 5,
												fontSize: 12,
												textAlign: 'center',
												fontWeight: '600',
												color: COLORS.FADED_TEXT,
											}}
										>
											{item.title}
										</Text>
									</TouchableOpacity>
								)}
							/>
						</>
					</View>
					<Heading text={'Select Model Year'} />
					<FlatList
						data={years}
						keyExtractor={(k) => k.id.toString()}
						numColumns={3}
						renderItem={({ item }) => (
							<TouchableOpacity
								style={{
									alignItems: 'center',
									justifyContent: 'space-between',
									flex: 1,
									margin: 5,
									borderRadius: 12,
									shadowColor: 'gray',
									shadowOpacity: 0.26,
									shadowOffset: { width: 0, height: 2 },
									shadowRadius: 10,
									elevation: 3,
								}}
								onPress={() => {
									setFormdata({ ...formdata, ['year']: item.year });
								}}
							>
								<Text
									style={{
										fontSize: 16,
										paddingVertical: 15,
										fontWeight: '600',
									}}
								>
									{item.year}
								</Text>
							</TouchableOpacity>
						)}
					/>
					<View>
						<Heading text={'KMs Driven'} />
						<RangeSlider
							min={0}
							max={20000}
							fromValueOnChange={(value) => {
								setFormdata({ ...formdata, ['fkms']: value });
								setFromValue(value);
							}}
							toValueOnChange={(value) => {
								setFormdata({ ...formdata, ['tkms']: value });
								setToValue(value);
							}}
							outOfRangeBarColor="#c8c8c8"
							showValueLabels={false}
							styleSize="small"
							inRangeBarColor="#0D4C92"
						/>

						<Text
							style={{
								textAlign: 'center',
								color: COLORS.FADED_TEXT,
								fontWeight: '500',
							}}
						>
							KMs Driven Between
						</Text>
						<Text
							style={{
								textAlign: 'center',
								color: COLORS.BLACK,
								fontWeight: '700',
								fontSize: 18,
							}}
						>
							{fromValue} KM - {toValue} KM
						</Text>
					</View>

					<View>
						<Heading text={'Price Value'} />
						<Slider
							min={0}
							max={1000000}
							valueOnChange={(value) => {
								setFormdata({ ...formdata, ['price']: value });
								setValue(value);
							}}
							outOfRangeBarColor="#c8c8c8"
							showValueLabels={false}
							styleSize="small"
							inRangeBarColor="#0D4C92"
						/>
						<Text
							style={{
								textAlign: 'center',
								color: COLORS.FADED_TEXT,
								fontWeight: '500',
							}}
						>
							Price Under
						</Text>
						<Text
							style={{
								textAlign: 'center',
								color: COLORS.BLACK,
								fontWeight: '700',
								fontSize: 18,
							}}
						>
							₹ {value} /-
						</Text>
					</View>
					<TouchableOpacity
						style={{
							backgroundColor: COLORS.ORANGE,
							padding: 5,
							borderRadius: 8,
							alignSelf: 'flex-end',
							margin: 10,
							width: 90,
						}}
						// onPress={sendData}
					>
						<Text
							style={{
								color: COLORS.WHITE,
								fontSize: 17,
								fontWeight: '700',
								textAlign: 'center',
							}}
						>
							LogIn
						</Text>
					</TouchableOpacity>
				</ScrollView>
			)}
		</>
	);
};

export default UsedCarModel;

const styles = StyleSheet.create({});
